import React, { Component } from 'react';

import ReactRevealText from 'react-reveal-text';

import downArrow from '../../images/down.svg';

import Typist from 'react-typist';
import 'react-typist/dist/Typist.css';

import './home.scss';



class Intro extends Component {

  constructor(props) {
    super(props);
    this.state = {
      showName: false,
      showSkills: false,
      skillClass: "fadein language"
    }
  }

  componentDidMount() {
    setTimeout(() => {

      this.setState({showName: true});
      setTimeout(() => {
        this.setState({showSkills: true});
        this.setState({skillClass: "language"});
      }, 2000);


    }, 1000);

  }
  render() {


    return (
      <div className="intro-container" id="home">

        <div className="fh col around">
          <ReactRevealText className="name-intro" show={this.state.showName}>
            Isitha Subasinghe
          </ReactRevealText>

          <ReactRevealText className="skills-intro" show={this.state.showSkills}>
            Full stack developer
          </ReactRevealText>

          {this.state.showSkills &&
            <div className="language">
              <Typist avgTypingDelay={150}>

                <span style={{marginRight: '2vw'}}>
                  Experienced in 
                </span>

                <span >
                  Java
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={4} delay={200} />

                <span>
                  React
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={5} delay={200} />

                <span>
                  AWS
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={3} delay={200} />

                <span>
                  Node
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={4} delay={200} />

                <span>
                  Python
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={6} delay={200} />

                <span>
                  Go
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={2} delay={200} />

                <span>
                  C
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={1} delay={200} />

                <span>
                  C++
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={3} delay={200} />

                <span>
                  Docker
                </span>
                <Typist.Delay ms={500} />
                <Typist.Backspace count={6} delay={200} />

                <span>
                  Kubernetes
                </span>
                
              </Typist>

            </div>
          }
           <a href="#about" className="fadein">
            <img src={downArrow} className="down-arrow" alt="Down chevron linking to about section"/>
           </a>
        </div>

       

      </div>
    )
  }
}

export default Intro;