import React, { Component } from 'react';
import github from '../../images/github.svg';
import linkedin from '../../images/linkedin.svg';
import Fade from 'react-reveal/Fade';
import './about.scss';
class About extends Component {

  render() {
    return (
      <div className="about-container" id="about">
        <div className="background">
          <div className="about-text">
            About
          </div>
        </div>

        <div className="about-grid">
          <Fade left cascade>
            <div className="skills">
              <div className="skills-intro skills-text">
                Some of my skills
              </div>
              <div className="skills-container">
                <div className="python">Python</div>
              </div>

              <div className="skills-container">
                <div className="java">Java</div>
              </div>

              <div className="skills-container">
                <div className="react">React</div>
              </div>

              <div className="skills-container">
                <div className="node">Node.js</div>
              </div>

              <div className="skills-container">
                <div className="javascript">Javascript</div>
              </div>

              
            </div>
          </Fade>
          
          <Fade right>
            <div className="about-body header">
              Hello I am Isitha Subasinghe, a fullstack developer and student
              with experience in many technologies including ReactJs,
              Spring, Node, AWS, Docker and Kubernetes. However, I am not 
              limited to just these technologies, I am a highly agile individual 
              who is capable of adapting to any tech stack.
            </div>
          </Fade>
        </div>
        <div className="footer">
          <div className="row around">      
            <a href="https://github.com/isubasinghe">
              <img className="footer-image" src={github} alt="Link to Isitha's github"/>
            </a>
            <a href="https://www.linkedin.com/in/isitha-subasinghe-a81881143/">
              <img className="footer-image" src={linkedin} alt="Link it Isitha's linkedin profile"/>
            </a>
          </div>
        </div>
      </div>
    )
  }
}

export default About;